table {
  width: 100%;
  text-align: left;
  border-spacing: 0;
  margin-bottom: 2rem;
  border-collapse: collapse;
}
table th {
  padding: 0.5rem;
  font-weight: bold;
  background: #471f4f;
}
table td {
  padding: 0.5rem;
}
table tr:nth-child(even) {
  background: rgba(20, 19, 23, 0.8470588235);
}
table tr.total {
  font-weight: bold;
}